import React from "react";
import styles from "./Projects.module.css";
import Esprin from "../../assets/images/esprin.jfif";

import M from "materialize-css";
const OtherProject = (props) => {
  document.addEventListener("DOMContentLoaded", function () {
    var elems = document.querySelectorAll(".tooltipped");
    M.Tooltip.init(elems, { enterDelay: 400 });
  });
  const Projects = [
    {
      title: "EsprIN",
      description:
        "Cross-platform app (web, desktop and mobile) that consists of a social network dedicated to the students of our university",
      image: Esprin,
      technologies: [
        { languge: "Symfony", description: "Web" },
        { languge: "Java", description: " Desktop" },
        { languge: "Codename", description: "Mobile" },
        { languge: "MySQL", description: "Database" },
        { languge: "Much More...âœŒ", description: "" },
      ],

      deployement:
        "https://www.linkedin.com/posts/ala-hamadi_teamwork-project-engineering-activity-6939544000903168000-IvGe?utm_source=share&utm_medium=member_desktop",
    },
  ];
  let ProjectsShow = Projects.slice(0, 6);
  if (props.show) {
    ProjectsShow = Projects.slice(0, Projects.length);
  }
  return (
    <div className='row'>
      {ProjectsShow.map((Project) => {
        return (
          <div key={Project.title} className='col-lg-4 col-md-6 col-sm-6'>
            <div className={`${styles.Card} card medium`}>
              <div
                className={`${styles.cardImage} card-image waves-effect waves-block waves-light`}
              >
                <img
                  alt='Screenshot of Interpol8r web app'
                  src={Project.image}
                  className='activator'
                />
              </div>
              <div className={`${styles.cardContent} card-content`}>
                <span
                  className={`${styles.cardTitle} ${styles.tealText} card-title activator teal-text hoverline`}
                >
                  {Project.title}
                  <i className={`fa fa-ellipsis-v right ${styles.Right}`}></i>
                </span>
                <p>{Project.description}</p>
              </div>
              <div className={`${styles.cardReveal} card-reveal`}>
                <span className={`${styles.cardTitle} card-title`}>
                  Accomplishments<i className='fa fa-times right'></i>
                </span>
                <ul>
                  {Project.technologies.map((technologie) => {
                    return (
                      <li>
                        <strong>{technologie.languge}</strong>{" "}
                        {technologie.description}
                      </li>
                    );
                  })}
                </ul>
                <div className={`${styles.cardAction} card-action`}>
                  <a
                    aria-label='Visit Interpol8r web app'
                    href={Project.deployement}
                    target='_blank'
                    rel='noopener noreferrer'
                    data-position='top'
                    data-tooltip='View Online'
                    className={`${styles.btnFloating} btn-floating btn-large waves-effect waves-light tooltipped`}
                  >
                    <i
                      className={`${styles.faExternalLink} fa fa-external-link`}
                    ></i>
                  </a>
                  {Project?.github && (
                    <a
                      aria-label='Visit the GitHub repo for Interpol8r project'
                      href={Project.github}
                      target='_blank'
                      rel='noopener noreferrer'
                      data-position='top'
                      data-tooltip='View Source'
                      className={`${styles.btnFloating} btn-floating btn-large waves-effect waves-light brown tooltipped`}
                    >
                      <i className='fa fa-github'></i>
                    </a>
                  )}
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default OtherProject;
