import React from "react";
import styles from "./SideDrawer.module.css";
import Backdrop from "../../UI/Backdrop/Backdrop";
import { Link } from "react-scroll";

const SideDrawer = (props) => {
  const links = ["Home", "Resume", "Projects", "Certifications"];
  let attachedClasses = `${styles.SideDrawer} ${styles.Close}`;
  if (props.open) attachedClasses = `${styles.SideDrawer} ${styles.Open}`;

  return (
    <React.Fragment>
      {styles.SideDrawer["display"] === "none" ? null : (
        <Backdrop show={props.open} clicked={props.closed} />
      )}
      <div className={attachedClasses} onClick={props.closed}>
        <span onClick={props.clicked}>
          My Portfolio<i className='fa fa-times right'></i>
        </span>
        <nav className={styles.Nav3}>
          <ul>
            {links.map((link) => {
              return (
                <li key={link}>
                  <Link to={link} smooth={true} duration={1000}>
                    {link}
                  </Link>
                </li>
              );
            })}
          </ul>
        </nav>
      </div>
    </React.Fragment>
  );
};

export default SideDrawer;
