import React from "react";
import styles from "./ProjectsFilter.module.css";

const ProjectsFilter = (props) => {
  return (
    <ul className={`${styles.galleryFilter} list`}>
      <li
        className={props.toogle === "all" ? styles.active : ""}
        onClick={() => props.clicked("all")}
      >
        <span>All</span>
      </li>
      <li
        className={props.toogle === "ReactJs" ? styles.active : ""}
        onClick={() => props.clicked("ReactJs")}
      >
        <span>React</span>
      </li>
      <li
        className={props.toogle === "React Native" ? styles.active : ""}
        onClick={() => props.clicked("React Native")}
      >
        <span>React Native</span>
      </li>
      <li
        className={props.toogle === "ExpressJs" ? styles.active : ""}
        onClick={() => props.clicked("ExpressJs")}
      >
        <span>NodeJs/ExpressJs</span>
      </li>
      <li
        className={props.toogle === "Other" ? styles.active : ""}
        onClick={() => props.clicked("Other")}
      >
        <span>Other</span>
      </li>
    </ul>
  );
};

export default ProjectsFilter;
